import { Indicator } from 'mint-ui';
import loginApi from '@/api/login.js'
export default {
	data() {
		return {
			username: '',
			password: '',
		};
	},
	methods:{
		login(){
			Indicator.open('Loading...');
			if(this.username && this.password){
				// 调取注册/登录接口
				let params = {
					name: this.username,
					pwd: this.password
				}
			
				loginApi.register(params).then((res)=>{
					console.log("调取注册/登录接口后返回到数据-----》",res)
					if(res && res.data.code === 1) {
						// 成功后 
						debugger
						Indicator.close();
						uni.switchTab({
							url:'../find/find',
							success: () => {
								uni.showToast({
									title:"登陆成功"
								})
							},
							fail: () =>{
								
							}
						}) 
					}else {
						Indicator.close();
					}
				})	
			}else {
				Indicator.close();
				uni.showModal({
					title: '提示',
					content: '用户名密码为空',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
